import React, { useState, useEffect } from 'react'
import dashboard from '../../models/dashboard'
import { Card, Col, Row, Spinner, Button } from 'react-bootstrap';
import { useHistory } from 'react-router';
import Chart from "react-google-charts";
import moment from "moment";
import { useSelector } from "react-redux";
export default function Dashboard() {
    const auth = useSelector(state => state.auth);
    const history = useHistory();
    const [isShowReceivedLoader, setisShowReceivedLoader] = useState(false)
    const [receivedAmount, setReceivedAmount] = useState([]);
    const [isShowPaidLoader, setisShowPaidLoader] = useState(false)
    const [paidAmount, setPaidAmount] = useState([]);
    const [emiCounts, setEmiCounts] = useState({notPaid:0,paid:0});
    useEffect(() => {
        getReceivedAmount();
        getPaidAmount();
        todayEmis();
    }, [])

    const getReceivedAmount = async () => {
        try {
            setisShowReceivedLoader(true);
            let response = await dashboard.DashboardModel.getReceivedAmount();
            setisShowReceivedLoader(false);
            if (response.statusCode == 200) {
                setReceivedAmount(response.body.message);
            } else {
                setReceivedAmount([]);
            }
        } catch (error) {
            setisShowReceivedLoader(false);
        }

    }
    const getPaidAmount = async () => {
        try {
            setisShowPaidLoader(true);
            let response = await dashboard.DashboardModel.getPaidAmount();
            setisShowPaidLoader(false);
            if (response.statusCode == 200) {
                setPaidAmount(response.body.message);
            } else {
                setPaidAmount([]);
            }
        } catch (error) {
            setisShowPaidLoader(false);
        }
    }
    const todayEmis = async()=>{
        let response = await dashboard.DashboardModel.getEmiRecord(moment().format("yyyy-MM-DD"));
        if(response?.body){
            setEmiCounts({notPaid:response.body.notPaidCount,paid:response.body.paidCount})
        }
    }
    const gotoLedger = (ledgerType) => {
        history.push("/ladger", ledgerType);
    }

    const formatAmount = (amount) => {
        return amount ? amount.toString().replace(/.(?=(..)*...$)/g, '$&,') : "";
    }
    return (
        <>
          {(auth.role=="checker"||auth.role=="admin")?<Row xs={1} md={3} className="g-2">
                <Col>
                    <Card
                        bg={"primary"}
                        key={0}
                        text={'dark'}
                        className="m-2"
                    >
                        {isShowReceivedLoader ? <Card.Body className="text-center"><Spinner animation="border" variant="dark" /></Card.Body> :
                            <Card.Body>
                                <Card.Title><h2 class="text-center">{formatAmount(receivedAmount?.total)}</h2></Card.Title>
                                <Card.Text class="text-center">

                                    Received Amount(Rs)
                                </Card.Text>
                            </Card.Body>}
                        <Card.Footer className="text-white text-center"><span style={{ "cursor": "pointer" }} className="" onClick={() => gotoLedger('totalReceived')}>More Info
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-right-circle" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8zm15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM4.5 7.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5z" />
                            </svg></span>
                        </Card.Footer>
                    </Card>
                </Col>

                <Col>
                    <Card
                        bg={"danger"}
                        key={0}
                        text={'dark'}
                        className="m-2"
                    >
                        {isShowPaidLoader ? <Card.Body className="text-center"><Spinner animation="border" variant="dark" /></Card.Body> :
                            <Card.Body>
                                <Card.Title><h2 class="text-center">{formatAmount(paidAmount?.total)}</h2></Card.Title>
                                <Card.Text class="text-center">

                                    Paid Amount(Rs)
                                </Card.Text>
                            </Card.Body>}
                        <Card.Footer className="text-white text-center"><span style={{ "cursor": "pointer" }} className="" onClick={() => gotoLedger('totalPaid')}>More Info
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-right-circle" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8zm15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM4.5 7.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5H4.5z" />
                            </svg></span>
                        </Card.Footer>

                    </Card>
                </Col>
                <Col>
                    <Card
                        bg={"success"}
                        key={0}
                        text={'dark'}
                        className="m-2"
                    >
                        {isShowReceivedLoader && isShowPaidLoader ? <Card.Body className="text-center"><Spinner animation="border" variant="dark" /></Card.Body> :
                            <Card.Body>
                                <Card.Title><h2 class="text-center">{formatAmount(receivedAmount?.total - paidAmount?.total)}</h2></Card.Title>
                                <Card.Text class="text-center">

                                    Cash in hand(Rs)
                                </Card.Text>
                            </Card.Body>}

                    </Card>
                </Col>
            </Row>:""}
            {(auth.role=="checker"||auth.role=="admin")?<Row xs={1} md={3} className="g-2">
                <Col>
                    <Card

                        key={0}
                        text={'dark'}

                        className="m-2">
                        <Card.Header className="text-center bg-warning">
                        <b>Main Ladger(Loan)</b>
                        </Card.Header>
                        <Card.Body className="text-center">
                            <Button variant="primary" onClick={() => { history.push("/completeLadger") }}>Go to Main Ladger</Button>
                        </Card.Body>
                    </Card>
                </Col>
                {/* <Col>
                    <Card
                        key={0}
                        text={'dark'}
                        className="m-2">
                        <Card.Header className="text-center bg-warning">
                        <b>Main Ladger(Saving Accounts)</b>
                        </Card.Header>
                        <Card.Body className="text-center">
                            <Button variant="primary" onClick={() => { history.push("/ladgerSavingAccount") }}>Go to Main Ladger</Button>
                        </Card.Body>
                    </Card>
                </Col> */}

            </Row>:""}
            <Row xs={1} md={3} className="g-2">
                <Col></Col>
                <Col>
                <Card

                    key={0}
                    text={'dark'}

                    className="m-2">
                    <Card.Header className="text-center bg-dark text-white">
                        Today's Due EMIs
                    </Card.Header>
                    <Card.Body className="text-center">
                    {emiCounts.paid==0 && emiCounts.notPaid==0?"No Due Emi today":
                    <Chart
                        
                        
                        chartType="PieChart"
                        loader={<div>Loading Chart</div>}
                        data={[
                            ['Task', 'Hours per Day'],
                            [`Paid (${emiCounts.paid})`, emiCounts.paid],
                            [`Not Paid (${emiCounts.notPaid})`, emiCounts.notPaid]
                        ]}
                        options={{
                            chartArea: { width: '100%', height: '100%' },
                            pieSliceText:"label"
                            // lineWidth: 25
                          }}
                        rootProps={{ 'data-testid': '0' }}
                    />}

                    </Card.Body>
                    </Card>

                </Col>
            </Row>
        </>
    )
}
