import {requests, superagent, API_ROOT} from "./BaseUrl"

//const url = '/memberGroups/';

const DashboardModel = {
  getReceivedAmount(){
    return superagent.get(`${API_ROOT}/dashboard/totalReceivedAmount`)
  },
  getPaidAmount(){
    return superagent.get(`${API_ROOT}/dashboard/totalPaidAmount`)
  },
  getEmiRecord(today){
    return superagent.get(`${API_ROOT}/emis/allEmis/${today}`);
  }
  }
    export default {DashboardModel};