import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { Table, Button, Container, Row, Col, Card, ListGroup,Modal,Toast } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import RDApplications from '../../models/RDApplications';

import Loader from '../layout/Loader';
import { useSelector } from "react-redux";
import { useReactToPrint } from 'react-to-print';
import { useRef } from 'react';
// import EmiCard from './EmiCard';
// import EmiCardPrint from './EmiCardPrint';
import { useHistory } from 'react-router-dom';

function RdDetails(props) {
    let history = useHistory();
    const memberDetailsRef = useRef();
    const handlePrintMemberDetail = useReactToPrint({
        content: () => memberDetailsRef.current,
        documentTitle:"AA2-Member",
      });

      const applicationRef = useRef();
      const handlePrintApplication = useReactToPrint({
          content: () => applicationRef.current,
          documentTitle:"AA2-application",
        });

        const emiRef = useRef();
        const handlePrintEMI = useReactToPrint({
            content: () => emiRef.current,
            documentTitle:"AA2-EMI-details",
          });
      
    const auth = useSelector(state => state.auth);
    const [paidEmiRecord, setPaidEmiRecord] = useState([])
    const [isShowLoader, setisShowLoader] = useState(false)
    const [isShowEMILoader, setisShowEMILoader] = useState(false)
    const [emiData, setEmiData] = useState([])
    const [loanDetails, setLoanDetails] = useState({});
    const [depositDetails, setDepositDetails] = useState([]);
    useEffect(() => {
        console.log(props);
        getLoanDetails();
    }, [])

    const getLoanDetails = async () => {
        try {
            setisShowLoader(true);
            let response = await RDApplications.RDApplicationsModel.getRdDetails(props.location.state);
            setisShowLoader(false);
            if (response.statusCode == 200) {
                if(response.body.message[0]["account_number"])
                    getDepositDetails(response.body.message[0]["account_number"])
                setLoanDetails(response.body.message[0]);
            }

        } catch (error) {
            setisShowLoader(false);
            console.log(error);
        }
    }

    const getDepositDetails = async (account_number) => {
        try {
            setisShowLoader(true);
            let response = await RDApplications.RDApplicationsModel.getDepositDetails(account_number);
            setisShowLoader(false);
            if (response.statusCode == 200) {
                setDepositDetails(response.body.message);
            }

        } catch (error) {
            setisShowLoader(false);
            console.log(error);
        }
    }
 
    const goback = ()=>{
        history.goBack();
    }
    return (
        <>
        <Button className="mt-2 ml-2" onClick={goback} size={"sm"}>Back</Button>
            <Loader show={isShowLoader} />

            <Row xs={1} md={2} className="g-1">
                <Col>
                    <Card border="success" header
                        key={0}
                        text={'dark'}
                        className="m-2"
                        ref={memberDetailsRef}
                    >
                        <Card.Header className="bg-success text-center"><b>Personal details</b><svg onClick={handlePrintMemberDetail} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-printer float-right cursar" viewBox="0 0 16 16">
                            <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
                            <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
                            </svg>
                        </Card.Header>
                        <Card.Body>
                            <Table size="sm" className="bg-white rounded" striped bordered hover responsive>
                                <tbody>
                                    <tr><th>Account holder name</th>  <td>{loanDetails.account_holder_name}</td></tr>
                                    <tr> <th>Father/Husband name</th>  <td>{loanDetails.father_husband_name}</td></tr>
                                    <tr><th>DOB</th>   <td>{loanDetails.dob?moment(loanDetails.dob).format("DD-MM-YYYY"):"" }</td></tr>
                                    <tr><th>Phone</th> <td>{loanDetails.phone}</td></tr>
                                    <tr><th>Gender</th>  <td>{loanDetails.gender}</td></tr>
                                    <tr><th>Aadhar No.</th> <td>{loanDetails.aadhar_card}</td></tr>
                                    <tr><th>PAN</th> <td>{loanDetails.pan_card}</td></tr>
                                    <tr><th>Driving License</th> <td>{loanDetails.driving_licence}</td></tr>
                                    <tr><th>Ration Card No.</th> <td>{loanDetails.ration_card}</td></tr>
                                    <tr><th>Voter ID No.</th> <td>{loanDetails.voter_id}</td></tr>

                                </tbody>
                            </Table>

                        </Card.Body>
                    </Card>
                </Col>
                <Col>
                    <Card border="info" header
                        key={0}
                        text={'dark'}
                        // style={{ width: '18rem' }}
                        className="m-2"
                        ref={applicationRef}
                    >
                        <Card.Header className="bg-info text-center"><b>RD Details</b><svg onClick={handlePrintApplication} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-printer float-right cursar" viewBox="0 0 16 16">
                        <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
                        <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
                        </svg>
                        </Card.Header>
                        <Card.Body>
                            
                            <Table size="sm" className="bg-white rounded" striped bordered hover responsive>
                                <tbody>
                                     <tr><th>Account Number</th><td>{loanDetails.account_number}</td> </tr>
                                    <tr><th>Account Open Date</th><td>{moment(loanDetails.created_at).format("DD-MM-YYYY HH:SS") }</td> </tr>
                                   <tr><th>Account Balance</th>   <td>{loanDetails.totalDeposited}</td></tr>
                                   <tr><th>Account Status</th>   <td>{loanDetails.is_account_closed==1?"Closed":loanDetails.is_account_closed==2?"Close Requested":"Active"}</td></tr>
                                <tr><th>Account Closed Amount</th><td>{loanDetails.is_account_closed==1?parseFloat(loanDetails.account_close_amount).toFixed(2):"-"}</td> </tr>

                                </tbody>
                            </Table>

                        </Card.Body>
                    </Card>
                </Col>

            </Row>
            <Row xs={1} md={2} className="g-1">
            <Col>
                <Card border="info" header
                    key={0}
                    text={'dark'}
                    // style={{ width: '18rem' }}
                    className="m-2"
                    ref={applicationRef}
                >
                    <Card.Header className="bg-info text-center"><b>Passbook</b><svg onClick={handlePrintApplication} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-printer float-right cursar" viewBox="0 0 16 16">
                    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
                    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
                    </svg>
                    </Card.Header>
                    <Card.Body>
                        
                        <Table size="sm" className="bg-white rounded" striped bordered hover responsive>
                            <thead className="bg-primary">
                                <tr>
                                    <th>Date</th>
                                    <th>Amount</th>
                                </tr>
                                </thead>
                                <tbody>
                                    {depositDetails.map(data=>data.is_deposited==1?(
                                    <tr>
                                        <td>{moment(data.deposited_date).format("DD-MM-YYYY")}</td>
                                        <td>{data.deposited_amount}</td>
                                    </tr>

                                    ):null)}
                                </tbody>
                        </Table>

                    </Card.Body>
                </Card>
                </Col>

            </Row>
        </>
    );
}

export default RdDetails;