import {requests, superagent, API_ROOT} from "./BaseUrl"

//const url = '/memberGroups/';

const RDApplicationsModel = {
  getAllRdApplications(agent_id){
    return superagent.get(`${API_ROOT}/rdApplication/entry/${agent_id}`);
  },
  openRdAccount(data){
    return superagent.post(`${API_ROOT}/rdApplication/entry`,data)
  },
  getRdDetails(id){
    return superagent.get(`${API_ROOT}/rdApplication/entryById/${id}`);
  },
  getDeposits(agent_id,deposites_date){
    return superagent.get(`${API_ROOT}/accountDeposited/entryByDate/${agent_id}/${deposites_date}`);
  },
  postDeposit(data){
    return superagent.post(`${API_ROOT}/accountDeposited/entry/`,data);

  },
  getDepositsGroupBy(agent_id,deposites_date){
    return superagent.get(`${API_ROOT}/accountDeposited/entryGroupBy/${agent_id}/${deposites_date}`);
  },
  getDepositsByAgent(agent_id,deposites_date){
    return superagent.get(`${API_ROOT}/accountDeposited/entryByDate/${agent_id}/${deposites_date}`);
  },

  calculateCloseAmount(account_number){
    return superagent.get(`${API_ROOT}/rdApplication/calculateCloseAmount/${account_number}`)
  },
  closeAmount(account_number){
    return superagent.post(`${API_ROOT}/rdApplication/closeAccount/`,account_number)
  },
  getDepositDetails(account_number){
    return superagent.get(`${API_ROOT}/accountDeposited/entryByAccount/${account_number}`)
  },

//   deleteGroup(group_code){
//     return superagent.del(`${API_ROOT}/memberGroups/entry/${group_code}`)
//   }

  }
    export default {RDApplicationsModel};