import React, { useEffect, useState } from 'react';
import { Table,Button, Container, Row, Col,Modal,Badge } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import masterRecord from '../../../models/masterRecord';
import Loader from '../../layout/Loader';
import moment from 'moment';
function MemberGroup(props) {
    const [isShowLoader, setisShowLoader] = useState(false)
    const [memberGroupRecords,setMemberGroupsRecords]=useState([]);
    const [memberList,setMemberList]=useState([]);
    const [showDeleteModel, setShowDeleteModel] = useState(false)
    const [deleteID, setDeleteID] = useState(0)

    const [showMemberModel, setshowMemberModel] = useState(false)
    const memberRecord = [];
    useEffect(() => {
            getMemberGroup();
    },[])

    const getMemberGroup = () =>{
        setisShowLoader(true);
        masterRecord.MemberGroupModel.getMemberGroups().then(res=>{
            setisShowLoader(false);
            if(res.statusCode == 200){
                setMemberGroupsRecords(res.body.message);
            }else{
                setMemberGroupsRecords([]);
            }
        })
    }
    
    const handleMemmberList = (group_code)=>{
        console.log(group_code);
        setisShowLoader(true);
        masterRecord.MemberGroupModel.getMembersBygroup(group_code).then(res=>{
            setisShowLoader(false);
            if(res.statusCode == 200){
                setMemberList(res.body.message);
                setshowMemberModel(true);
            }else{
                setMemberList([]);
            }
        })

    }

    const deleteHandle=(id)=>{
        setDeleteID(id);        
        setShowDeleteModel(true);
    }
    const deleteMemberGroup=async()=>{
        setShowDeleteModel(false);
        try{
            setisShowLoader(true);
            let response = await masterRecord.MemberGroupModel.deleteGroup(deleteID);
            setisShowLoader(false);
            if(response.statusCode == 200){
                let UpdatedList = memberGroupRecords.filter((member,idx)=>member.group_code!=deleteID);
                setMemberGroupsRecords(UpdatedList);
             }
        }catch (error) {
                console.log(error.response.body.message);
                setisShowLoader(false);
            }
              
    }

    return (
        <>
      <Modal show={showDeleteModel} onHide={()=>setShowDeleteModel(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Delete Group</Modal.Title>
        </Modal.Header>
        <Modal.Body>Are you sure?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={()=>setShowDeleteModel(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={()=>deleteMemberGroup()}>
            Delete
          </Button>
        </Modal.Footer>
      </Modal>        

      <Modal show={showMemberModel} onHide={()=>setshowMemberModel(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Members</Modal.Title>
        </Modal.Header>
        <Modal.Body>{memberList.length!=0?<Table className="bg-white rounded small" striped bordered hover responsive>
        <thead className="bg-primary">
            <tr>
                <th>ID</th>
                <th>Name</th>
                <th>Gender</th>
                <th>DOB</th>
                <th>age</th>
                <th>Marital status</th>
                <th>Mobile</th>
                <th>Aadhar</th>
                <th>Voter id</th>
                <th>PAN</th>
                <th>Driving licence</th>
                <th>Ration card</th>
                <th>Bank IFSC code</th>
                <th>Bank account</th>
                <th>Bank account holder</th>
            </tr>
            </thead> 
                <tbody>
                {memberList.map(val=>
            <tr>
                <td>{val.member_id}</td>
                <td>{val.member_name}</td>
                <td>{val.gender}</td>
                <td>{moment(val.date_of_birth).format("DD-MM-YYYY") }</td>
                <td>{val.age}</td>
                <td>{val.marital_status}</td>
                <td>{val.mobile_number}</td>
                <td>{val.aadhar_number}</td>
                <td>{val.voter_id_number}</td>
                <td>{val.pan_card_number}</td>
                <td>{val.driving_license_number}</td>
                <td>{val.ration_card_number}</td>
                <td>{val.bank_ifsc_code}</td>
                <td>{val.bank_account}</td>
                <td>{val.bank_account_holder}</td>
            </tr>
            )}
            </tbody></Table>:<h5>No members in this group!</h5>}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={()=>setshowMemberModel(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={()=>setshowMemberModel(false)}>
            Ok
          </Button>
        </Modal.Footer>
      </Modal>        

        <Loader show={isShowLoader}/>
        <Container fluid>
        <h2 className="text-info text-center">Member Groups</h2>
            <Row className="mb-5 mt-3">
                <Col>
                <Link to="/addMemberGroup">
                <Button variant="info" size="sm" active>
                    Add Member Group
                </Button>
                </Link>
                </Col>
            </Row>
            <Row>
            <Col>
            <Table className=" shadow-lg p-3 mb-5 bg-white rounded" striped bordered hover responsive>
            <thead className="bg-primary">
                <tr>
                    <th>Group Code</th>
                    <th>Name</th>
                    <th>Remark</th>
                    <th>Member</th>
                    <th>Action</th>
                </tr>
            </thead>
            <tbody>
                {memberGroupRecords.map((group,id)=>(<tr>
                    <td>{group.group_code}</td>
                    <td>{group.group_name}</td>
                    <td>{group.remark}</td>
                    <td><Button size="sm" variant="info" onClick={()=>handleMemmberList(group.group_code)}>
                            Members<Badge className="bg-secondary">{group.member_count}</Badge></Button></td>
                    <td><Button size="sm" disabled={group.member_count==0?false:true} variant="danger" onClick={()=>deleteHandle(group.group_code)}>
                        Delete</Button></td>
                </tr>))}
            </tbody>
        </Table>
</Col>
            </Row>
        </Container>
        </>
        );
}

export default MemberGroup;