import moment from 'moment';
import React from 'react';
import { Table } from 'react-bootstrap';

class PrintRdApplication extends React.PureComponent {
    render() {
        return (
            <div>
            <h3 className="text-center mb-5 mt-3">AAs2 MutualHelp Micro Finance</h3>
            <Table className=" shadow-lg p-3 mb-5 bg-white rounded small" striped bordered hover responsive>
                <thead className="bg-primary">
                    <tr>
                        <th>Agent Name</th>
                        <th>Account No.</th>
                        <th>Account Holder</th>
                        <th>Address</th>
                        <th>Phone</th>
                        <th>Account Balance</th>
                        <th>Account Status</th>
                        <th>Account Opening Date</th>
                    </tr>
                </thead>
                <tbody>
                    {this.props.dueEmis.map((emi, id) => (<tr>
                        <td>{emi.values.col3}</td>
                        <td>{emi.values.col1}</td>
                        <td>{emi.values.col2}</td>
                        <td>{emi.values.col4}</td>
                        <td>{emi.values.col5}</td>
                        <td>{emi.values.col6}</td>
                        <td>{emi.values.col7==1?"Closed":emi.values.col7==2?"Close Requested":"Active"}</td>
                        <td>{moment(emi.values.col8).format("DD-MM-YYYY HH:SS")}</td>
                    </tr>))}
                </tbody>
                <tfoot>
                {/* <tr>
                <th>Total Collection</th><th colSpan="9">{this.props.dueEmis.reduce(this.add,0)}</th>
                    
                </tr> */}
                </tfoot>
            </Table> 
            

            </div>
        );
    }
}

export default PrintRdApplication;