import React,{useState,useEffect,useRef} from 'react';
import dashboard from '../../models/dashboard'
import { Table, Button, Row, Col, Card, Spinner} from 'react-bootstrap';
import moment from 'moment';
function Ladger(props) {
    const [isShowLoaderLoader, setisShowLoaderLoader] = useState(false)
    const [ladger,setLadger] = useState([]);
    const refContainer = useRef("");
    useEffect(()=>{
        if(props.location.state == "totalReceived"){
            refContainer.current = "Received Details";
            getReceivedAmount();
        }else if(props.location.state == "totalPaid"){
            refContainer.current = "Paid Details";
            getPaidAmount();
        }
    },[])
    const getReceivedAmount=async()=>{
        try {
            setisShowLoaderLoader(true);
            let response = await dashboard.DashboardModel.getReceivedAmount();
            setisShowLoaderLoader(false);
            if (response.statusCode == 200) {
               let emiLadger = []
               let processingFeeLadger = []
               let data = []
               emiLadger = response.body.message?.emis.map((val,idx)=>{
                   return {
                            "date":moment(val.EMI_date).format("DD-MM-yyyy"),
                            "particular":`EMI received Loan Account - ${val.loan_account_no}`,
                            "amount":val.EMI_amount,
                    }
               });
               processingFeeLadger = response.body.message?.processingFee.map((val,idx)=>{
                   return {
                            "date":moment(val.created_at).format("DD-MM-yyyy"),
                            "particular":val.particular,
                            "amount":val.amount,
                            }
               });
               data = [...emiLadger,...processingFeeLadger];
               data.sort( compare );
               setLadger(data);
            } else {
                setLadger([]);
            }
        } catch (error) {
            setisShowLoaderLoader(false);
          }
    }
    const getPaidAmount=async()=>{
        try {
            setisShowLoaderLoader(true);
            let response = await dashboard.DashboardModel.getPaidAmount();
            setisShowLoaderLoader(false);
            if (response.statusCode == 200) {
               let expensesLadger = []
               let loanLadger = []
               let data = []
               expensesLadger = response.body.message?.expenses.map((val,idx)=>{
                   return {
                            "date":moment(val.date_of_expense).format("DD-MM-yyyy"),
                            "particular":val.expense_type,
                            "amount":val.amount,
                    }
               });
               loanLadger = response.body.message?.loan.map((val,idx)=>{
                   return {
                            "date":moment(val.application_date).format("DD-MM-yyyy"),
                            "particular":`Loan disbursed to ${val.member_name} - Loan account ${val.loan_account_no}`,
                            "amount":val.loan_amount,
                    }
               });
               data = [...expensesLadger,...loanLadger];
               data.sort( compare );
               setLadger(data);
            } else {
                setLadger([]);
            }
        } catch (error) {
            setisShowLoaderLoader(false);
        }
    }
    const compare=( a, b )=>{
        if ( a.date < b.date ){
          return -1;
        }
        if ( a.date > b.date ){
          return 1;
        }
        return 0;
    }
    const formatAmount = (amount) =>{
        return amount?amount.toString().replace(/.(?=(..)*...$)/g, '$&,'):"";
    }

    return (
        <>
            <Row >
                <Col>
                <Card border="success" header
                        key={0}
                        text={'dark'}
                        className="m-2">
                    <Card.Header className="bg-success text-center"><b>{refContainer.current} </b></Card.Header>
                    {isShowLoaderLoader?<Card.Body className="text-center"><Spinner animation="border"   variant="dark"/></Card.Body> :
                        <Card.Body>
                        <Table size={"sm"} className="bg-white rounded small" bordered responsive>
                        <thead>
                        <tr className="bg-success">
                            <th>SR No.</th>
                            <th>Date</th>
                            <th>Particular</th>
                            <th>Amount</th>
                        </tr>
                        </thead>
                            <tbody>
                                {ladger.map((val,idx)=><tr>
                                    <td>
                                        {idx+1}
                                    </td>
                                    <td>
                                        {val.date}
                                    </td>
                                    <td>
                                        {val.particular}
                                    </td>
                                    <td >
                                        {formatAmount(val.amount)}
                                    </td>

                                </tr>)}
                            </tbody>
                            <tfoot>
                                <tr>
                                    <th>Total</th>
                                    <th colSpan="3" className="text-right">{ladger.reduce(function (previous, current) {
                                            return previous + parseFloat(current.amount);
                                        }, 0)}</th>
                                </tr>
                            </tfoot>
                </Table>
                        </Card.Body> }  
                </Card>
                </Col>
            </Row>
            
        </>
    );
}

export default Ladger;