import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Row, Col, Toast } from 'react-bootstrap';
import { useFormik } from 'formik';
import DatePicker from "react-datepicker";
import { useHistory } from 'react-router-dom';
import RDApplications from '../../models/RDApplications';
import Loader from '../layout/Loader';
import { useSelector } from "react-redux";

function OpenNewRdAccount(props) {
    const auth = useSelector(state => state.auth);
    const [loading, setLoading] = useState(false);
    const [dob, setdob] = useState(new Date());
    const [showToast, setShowToast] = useState({ isShow: false, type: "", message: "" })

    // const [mode, setMode] = useState('date');
    // const [show, setShow] = useState(false);
    // const [selectedTenure, setSelectedTenure] = useState();
    // const [selectedInterest, setSelectedInterest] = useState();
    const [checked, setChecked] = useState(false);
    let history = useHistory();
    let secondTextInput = "";
    const validate = values => {
        const errors = {};
        if (!values.account_holder_name) {
            errors.account_holder_name = 'Account holder name is Required';
        }
        if (!values.rd_amount) {
            errors.rd_amount = 'RD amount  is Required';
        }
        if (!values.period) {
            errors.period = 'Period is Required';
        }
        if (!values.interest_rate) {
            errors.interest_rate = 'Interest rate is Required';
        }
        if (!values.tenure) {
            errors.tenure = 'Tenure is Required';
        }

        return errors;
    };
    // const onChange = (event, selectedDate) => {
    //   secondTextInput.focus();

    //   const currentDate = selectedDate || date;
    //   setShow(Platform.OS === 'ios');
    //   setDate(currentDate);

    // };

    // const showMode = (currentMode) => {
    //   setShow(true);
    //   setMode(currentMode);
    // };

    // const showDatepicker = () => {
    //   showMode('date');
    // };

    const formik = useFormik({
        initialValues: {
            account_holder_name: '',
            father_husband_name: '',
            dob: '',
            address: '',
            phone: '',
            aadhar_card: '',
            pan_card: '',
            driving_licence: '',
            voter_id: '',
            ration_card: '',
            interest_rate: '',
            tenure: '',
            rd_amount: '',
            period: '',
            agent_id: '',
            gender: '',
            initial_deposited_amount: ''
        },
        validate,
        onSubmit: async (values) => {
            setLoading(true);
            values.dob = dob;
            values.agent_id = auth.id;
            values.initial_deposited_amount = checked ? values.rd_amount : "";
            try {
                let response = await RDApplications.RDApplicationsModel.openRdAccount(values);
                console.log(response);
                setLoading(false);
                if (response.status == 200) {
                    formik.resetForm();
                    setShowToast({ isShow: true,type:"bg-success", message: response.body.message })
                } else {
                    setShowToast({ isShow: true,type:"bg-danger", message: response.body.message })
                }
            } catch (error) {
                console.log(error);
                setLoading(false);
                setShowToast({ isShow: true,type:"bg-danger", message: error.response.body.message })
            }
        },
    });

    return (
        <>
        <Toast key={1} autohide delay={3000} show={showToast.isShow} onClose={() => setShowToast({ isShow: false,type:"", message: "" })} className={"loader "+ (showToast.type) } >
            <Toast.Header>
            <strong className="me-auto">{showToast.type=="bg-danger"?"Error":"Success"} Message</strong>
            </Toast.Header>
            <Toast.Body className="Dark">
            {showToast.message}
            </Toast.Body>
        </Toast>
            <Loader show={loading} />
            <h2 className="text-info text-center">Open New RD Account</h2>
            <Container fluid className="bg-white mt-5 shadow-lg p-3 mb-5 bg-white rounded">
                <Form onSubmit={formik.handleSubmit}>
                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>Account holder name</Form.Label>
                            <Form.Control
                                name="account_holder_name"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.account_holder_name}
                            />
                            {formik.touched.account_holder_name && formik.errors.account_holder_name ? (
                                <div class="text-danger">{formik.errors.account_holder_name}</div>
                            ) : null}
                        </Form.Group>
                        <Form.Group as={Col} className="form-group required" controlId="formGridEnroll">
                            <Form.Label >Date of Birth</Form.Label>
                            <DatePicker class="form-control"
                                selected={dob}
                                onChange={(date) => setdob(date)}
                                name="application_date"
                            />
                        </Form.Group>
                    </Row>

                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>Father/Husband name</Form.Label>
                            <Form.Control
                                name="father_husband_name"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.father_husband_name}
                            />

                            {formik.touched.father_husband_name && formik.errors.father_husband_name ? (
                                <div class="text-danger">{formik.errors.father_husband_name}</div>
                            ) : null}

                        </Form.Group>

                        <Form.Group as={Col} controlId="formGriddob">
                            <Form.Label>Address</Form.Label>
                            <Form.Control
                                name="address"
                                as="textarea"
                                onChange={formik.handleChange}
                                value={formik.values.address}

                            />
                        </Form.Group>
                    </Row>

                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>phone</Form.Label>
                            <Form.Control
                                name="phone"
                                type="number"
                                onChange={formik.handleChange}
                                value={formik.values.phone}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGriddob">
                            <Form.Label>gender</Form.Label>
                            <select
                                className="form-control"
                                name="gender"
                                onChange={formik.handleChange}
                                value={formik.values.gender}>
                                <option key="" value="">Select gender</option>
                                <option key="male" value="male">Male</option>
                                <option key="female" value="female">female</option>
                            </select>
                        </Form.Group>
                    </Row>

                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob" className="form-group">
                            <Form.Label>Aadhar card number</Form.Label>
                            <Form.Control
                                name="aadhar_card"
                                type="number"
                                onChange={formik.handleChange}
                                value={formik.values.aadhar_card}
                            />
                        </Form.Group>
                        <Form.Group as={Col} controlId="formGriddob" className="form-group">
                            <Form.Label>PAN</Form.Label>
                            <Form.Control
                                name="pan_card"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.pan_card}
                            />
                        </Form.Group>
                    </Row>

                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob" className="form-group">
                            <Form.Label>Driving Licence</Form.Label>
                            <Form.Control
                                name="driving_licence"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.driving_licence}
                            />
                        </Form.Group>
                        <Form.Group as={Col} controlId="formGriddob" className="form-group">
                            <Form.Label>Voter Id</Form.Label>
                            <Form.Control
                                name="voter_id"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.voter_id}
                            />
                        </Form.Group>

                    </Row>

                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob" className="form-group">
                            <Form.Label>Ration card</Form.Label>
                            <Form.Control
                                name="ration_card"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.ration_card}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>Interest Rate (in %)</Form.Label>
                            <Form.Control
                                name="interest_rate"
                                type="number"
                                min="0"
                                onChange={formik.handleChange}
                                value={formik.values.interest_rate}
                            />
                            {formik.touched.interest_rate && formik.errors.interest_rate ? (
                                <div class="text-danger">{formik.errors.interest_rate}</div>
                            ) : null}

                        </Form.Group>

                    </Row>

                    <Row className="mb-3">
                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>RD Amount</Form.Label>
                            <Form.Control
                                name="rd_amount"
                                type="number"
                                min="0"
                                onChange={formik.handleChange}
                                value={formik.values.rd_amount}
                            />
                            <Form.Text className="text-muted">
                                It will be per month or daily. Based on tenure.
                            </Form.Text>

                            {formik.touched.rd_amount && formik.errors.rd_amount ? (
                                <div class="text-danger">{formik.errors.rd_amount}</div>
                            ) : null}

                        </Form.Group>

                        <Form.Group as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>Tenure</Form.Label>
                            <select
                                className="form-control"
                                name="tenure"
                                onChange={formik.handleChange}
                                value={formik.values.tenure}
                            >
                                <option key="" value="">Select Tenure</option>
                                {/* <option key="daily" value="daily">Daily</option> */}
                                <option key="daily" value="daily">Daily</option>
                                <option key="monthly" value="monthly">Monthly</option>
                            </select>
                            {formik.touched.tenure && formik.errors.tenure ? (
                                <div class="text-danger">{formik.errors.tenure}</div>
                            ) : null}

                        </Form.Group>
                    </Row>

                    <Row className="mb-3">
                        <Form.Group md={6} as={Col} controlId="formGriddob" className="form-group required">
                            <Form.Label>Period (in Months)</Form.Label>
                            <Form.Control
                                name="period"
                                type="number"
                                min="0"
                                onChange={formik.handleChange}
                                value={formik.values.period}
                            />
                            {formik.touched.period && formik.errors.period ? (
                                <div class="text-danger">{formik.errors.period}</div>
                            ) : null}
                        </Form.Group>
                    </Row>

                    <Button variant="primary" type="submit">
                        Save
                    </Button>
                    <Button variant="danger" onClick={() => { history.push("/") }} type="button" className="ml-2">
                        Cancel
                    </Button>
                </Form>
            </Container>
        </>
    );
}

export default OpenNewRdAccount;