import React, { useState, useEffect,useRef } from 'react';
import dashboard from '../../models/dashboard'
import { Table, Button, Row, Col, Card, Spinner, ProgressBar } from 'react-bootstrap';
import moment from 'moment';
import { useReactToPrint } from 'react-to-print';

function CompleteLadger(props) {
    const [ladger, setLadger] = useState([]);
    const [progress, setProgress] = useState(0);
    const emiDetailsRef = useRef();

    const handlePrintEmiDetail = useReactToPrint({
        content: () => emiDetailsRef.current,
        documentTitle:"AA2-Due-EMIs",
      });

    let balance = 0;
    useEffect(() => {
        async function fetchData() {
            let received = [];
            let paid = [];
            let ladgerArray = [];
            received = await getReceivedAmount();
            paid = await getPaidAmount();
            ladgerArray = [...received, ...paid];
            ladgerArray.sort(compare);
            setLadger(ladgerArray);
          }
          fetchData();
    }, [])
    const getReceivedAmount = async () => {
        try {
            setProgress(10);
            let response = await dashboard.DashboardModel.getReceivedAmount();
            setProgress(50);
            if (response.statusCode == 200) {
                let emiLadger = []
                let processingFeeLadger = []
                let data = []
                emiLadger = response.body.message?.emis.map((val, idx) => {
                    return {
                        "date": moment(val.EMI_date).format("DD-MM-yyyy"),
                        "particular": `EMI received Loan Account - ${val.loan_account_no}`,
                        "cr": val.EMI_amount,
                        "dr": "",
                    }
                });
                processingFeeLadger = response.body.message?.processingFee.map((val, idx) => {
                    return {
                        "date": moment(val.created_at).format("DD-MM-yyyy"),
                        "particular": val.particular,
                        "cr": val.amount,
                        "dr": "",
                    }
                });
                 data = [...emiLadger, ...processingFeeLadger];
                 return data;
                // data.sort(compare);
                // setLadger(data);
            } else {
                // setLadger([]);
                return [];
            }
        } catch (error) {
            setProgress(100);
        }
    }
    const getPaidAmount = async () => {
        try {
            setProgress(70);
            let response = await dashboard.DashboardModel.getPaidAmount();
            setProgress(90);
            if (response.statusCode == 200) {
                let expensesLadger = []
                let loanLadger = []
                let data = []
                expensesLadger = response.body.message?.expenses.map((val, idx) => {
                    return {
                        "date": moment(val.date_of_expense).format("DD-MM-yyyy"),
                        "particular": val.expense_type,
                        "cr": "",
                        "dr": val.amount,
                    }
                });
                loanLadger = response.body.message?.loan.map((val, idx) => {
                    return {
                        "date": moment(val.application_date).format("DD-MM-yyyy"),
                        "particular": `Loan disbursed to ${val.member_name} - Loan account ${val.loan_account_no}`,
                        "dr": val.loan_amount,
                        "cr": "",
                    }
                });
                 data = [ ...expensesLadger, ...loanLadger];
                 setProgress(100);
                 return data;
                // data.sort(compare);
                // setLadger(data);
            } else {
                //setLadger([]);
                return [];
            }
        } catch (error) {
            setProgress(100);
        }
    }
    const compare = (a, b) => {
        if (a.date < b.date) {
            return -1;
        }
        if (a.date > b.date) {
            return 1;
        }
        return 0;
    }
    const calculateTotal = (dr, cr) => {
        let total = balance;
        balance = dr != "" ? total - parseFloat(dr) : total + parseFloat(cr);
        return balance;
    }
    const formatAmount = (amount) => {
        return amount ? amount.toString().replace(/.(?=(..)*...$)/g, '$&,') : "";
    }

    return (
        <>
            <Row >
                <Col>
                
                    <Card border="success" header
                        key={0}
                        text={'dark'}
                        
                        className="m-2">
                        <Card.Header className="bg-success text-center"><b>Ledger </b>
                        <svg style={{cursor:'pointer',float:"right"}} onClick={handlePrintEmiDetail} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-printer mb-2" viewBox="0 0 16 16">
            <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
            <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
            </svg>
                        </Card.Header>
                        {progress<100 ? <Card.Body className="text-center"><div style={{ top: '50%',position:"relative" }}><ProgressBar style={{"position":"relative","width":"30%","left":"40%"}} striped variant="info" now={progress} label={`${progress}%`} /></div></Card.Body> :
                            <Card.Body>
                                <Table ref={emiDetailsRef} size={"sm"} className="bg-white rounded small" bordered responsive>
                                    <thead>
                                        <tr className="bg-success">
                                            <th>SR No.</th>
                                            <th>Date</th>
                                            <th>Particular</th>
                                            <th>DR</th>
                                            <th>CR</th>
                                            <th>Balance</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {ladger.map((val, idx) => <tr>
                                            <td>
                                                {idx + 1}
                                            </td>
                                            <td>
                                                {val.date}
                                            </td>
                                            <td>
                                                {val.particular}
                                            </td>
                                            <td>
                                                {formatAmount(val.dr)}
                                            </td>
                                            <td>
                                                {formatAmount(val.cr)}
                                            </td>
                                            <td>
                                                {formatAmount(calculateTotal(val.dr, val.cr))}
                                            </td>

                                        </tr>)}
                                    </tbody>
                                </Table>
                            </Card.Body>}
                    </Card>
                </Col>
            </Row>

        </>
    );
}

export default CompleteLadger;