import React, { useEffect, useState, useMemo, useRef } from 'react';
import { Table,Button, Container, Row, Col,Modal,Pagination, Badge } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import RDApplications from '../../models/RDApplications';
import User from '../../models/user';
import Loader from '../layout/Loader';
import moment from 'moment';
import {useTable,useSortBy,usePagination,useFilters } from 'react-table'
import { useHistory } from 'react-router-dom';
import { useSelector,useDispatch } from "react-redux";
import PrintRdApplication from './PrintRdApplication';
import { useReactToPrint } from 'react-to-print';

function RdApplications(props) {
  const emiDetailsRef = useRef();
  const handlePrintEmiDetail = useReactToPrint({
    content: () => emiDetailsRef.current,
    documentTitle:"AA2-Accounts",
  });

    const auth = useSelector(state => state.auth);
    let history = useHistory();
    const [isShowLoader, setisShowLoader] = useState(false);
    const [userList, setUserList] = useState([]);
    const [showDeleteModel, setShowDeleteModel] = useState(false)
    const [selectedAccountID, setselectedAccountID] = useState({"account_number":"","totalAmount":""})
    const [rdAccounts,setRdAccounts]=useState([]);
    useEffect(() => {
            getMemberGroup();
    },[])
    
    const data = useMemo(
        () => 
        rdAccounts.map((record,index)=>
                { 
             return {
                col3:record.agent_name,
                col1:record.account_number,
                col2:record.account_holder_name,
                col4:record.address,
                col5:record.phone,
                col6:record.totalDeposited,
                col7:record.is_account_closed,
                col8:record.created_at,
                col9:record,
                }
            }
        )
        ,
        [rdAccounts]
      )   

    const columns = useMemo(
        () => [
          {
            Header: 'Agent name',
            accessor: 'col3', // accessor is the "key" in the data
            allowFilter:true,
           
          },
          {
            Header: 'Account number',
            accessor: 'col1', // accessor is the "key" in the data
            allowFilter:true,
        },
          {
            Header: 'Account holder',
            accessor: 'col2',
            allowFilter:true
          },
          {
            Header: 'Address',
            accessor: 'col4',
            allowFilter:true
          },
          {
            Header: 'Phone',
            accessor: 'col5',
            allowFilter:true
          },
          {
            Header: 'Account Balance',
            accessor: 'col6',
            allowFilter:true
          },
          {
            Header: 'Account Status',
            accessor: 'col7',
            allowFilter:true,
            Cell:emi =><Badge className={emi.value==1?"bg-danger":emi.value==2?"bg-warning":"bg-success"}>{emi.value==1?"Closed":emi.value==2?"Close Requested":"Active"}</Badge>
          },
          {
            Header: 'Account Opening Date',
            accessor: 'col8',
            allowFilter:true,
            Cell:emi =>moment(emi.value).format("DD-MM-YYYY HH:SS")

          },
          {
            Header: 'Action',
            accessor: 'col9',
            allowFilter: false,
            Cell: emi => {
                return (<><Button size={"sm"} variant="info" onClick={() => { history.push("/rdDetails", emi.value.id) }} type="button" className="m-2">
                    View
                </Button>
                <Button size={"sm"} variant="danger" disabled={emi.value.is_account_closed==1?true:false} onClick={() =>handleCloseAccount(emi.value.account_number)} type="button" >
                    Close Account
                </Button>
                </>
                )
            }

        }





        ],
        []
      )    
    const getMemberGroup = () =>{
        setisShowLoader(true);
        let formatedAccounts = [];
        let payload = auth.id;
        if(auth.role == "admin" || auth.role == "maker"|| auth.role == "checker"){
          payload = "all";
        }
        RDApplications.RDApplicationsModel.getAllRdApplications(payload).then(async(res)=>{
            setisShowLoader(false);
            if(res.statusCode == 200){
              let list = await User.Auth.getUsers();
              //setUserList(list);
              formatedAccounts = res.body.message.map(ac=>{
                let obj = list.body.message.filter(user=>user.id == ac.agent_id)
                ac["agent_name"] = obj[0]["name"];
                return ac;
              });
              setRdAccounts(formatedAccounts);
            }else{
                setRdAccounts([]);
            }
        })
    }
    // const deleteHandle=(id)=>{
    //     setDeleteID(id);        
    //     setShowDeleteModel(true);
    // }
    const handleCloseAccount=async(account_number)=>{
        //setShowDeleteModel(false);
        try{
            setisShowLoader(true);
            let response = await RDApplications.RDApplicationsModel.calculateCloseAmount(account_number);
            setisShowLoader(false);
            if(response.statusCode == 200){
              setselectedAccountID({
                "account_number":account_number,
                "totalAmount":response.body.message
              })
              setShowDeleteModel(true);
               // let UpdatedList = memberRecords.filter((member,idx)=>member.member_id!=deleteID);
                //setMemberRecords(UpdatedList);
             }
        }catch (error) {
                console.log(error.response.body.message);
                setisShowLoader(false);
            }
              
    }
  
    function DefaultColumnFilter({
        column: { filterValue, preFilteredRows, setFilter },
    }) {
        const count = preFilteredRows.length
    
        return (
            <input
                className="form-control"
                value={filterValue || ''}
                onChange={e => {
                    setFilter(e.target.value || undefined)
                }}
                placeholder={`Search ${count} records...`}
            />
        )
    }    
    const defaultColumn = React.useMemo(
        () => ({
            // Default Filter UI
            Filter: DefaultColumnFilter,
        }),
        []
    ) 
    const paginate = (totalPage)=>{
      let pageNumberArray = [];
      for(let i=1;i<=totalPage;i++){
        pageNumberArray.push(i);
      }
      return(      
      <Pagination>
        <Pagination.First  onClick={() => gotoPage(0)}/>
        <Pagination.Prev onClick={() => previousPage()} disabled={!canPreviousPage}/>
        {pageNumberArray.map(num=><Pagination.Item key={num} active={num === (pageIndex+1)} onClick={() => gotoPage(num-1)}>{num}</Pagination.Item>)}
        <Pagination.Next onClick={() => nextPage()} disabled={!canNextPage}/>
        <Pagination.Last onClick={() => gotoPage(totalPage-1)}/>
        </Pagination>
      );
    
    }
    const closeRdAccount=async()=>{
      try{
        setisShowLoader(true);
        let response = await RDApplications.RDApplicationsModel.closeAmount({"account_number":selectedAccountID.account_number,"agent_id":auth.id});
        setisShowLoader(false);
        if(response.statusCode == 200){
          setShowDeleteModel(false);
          getMemberGroup();
         }
    }catch (error) {
            console.log(error.response.body.message);
            setisShowLoader(false);
        }

    }
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        pageOptions,
        page,
        pageCount,
        state: { pageIndex, pageSize },
        gotoPage,
        previousPage,
        nextPage,
        setPageSize,
        canPreviousPage,
        canNextPage,  
      } = useTable({ columns, data, defaultColumn, initialState: { pageIndex: 0 }},useFilters,useSortBy,usePagination)
    return (
        <>
      <Modal show={showDeleteModel} onHide={()=>setShowDeleteModel(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Close Account - {selectedAccountID["account_number"]}</Modal.Title>
        </Modal.Header>
        <Modal.Body>Total Maturity Amount is Rs {parseFloat(selectedAccountID["totalAmount"]).toFixed(2)}. Are you sure. You want to close? </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={()=>setShowDeleteModel(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={()=>closeRdAccount()}>
            Close Account
          </Button>
        </Modal.Footer>
      </Modal>        
        <Loader show={isShowLoader}/>
        <Container fluid>
        <h2 className="text-info text-center">RD applications</h2>
            <Row className="mb-5 mt-3">
                <Col>
                <Link to="/openNewRdAccount">
                <Button variant="info"  active>
                    Open new RD
                </Button>
                </Link>
                </Col>
            </Row>
            <Row>
            <Col>
            <div className="text-right">
              {paginate(pageCount)}
              <svg style={{cursor:'pointer'}} onClick={handlePrintEmiDetail} xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-printer mb-2" viewBox="0 0 16 16">
            <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
            <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
            </svg>

            </div>   

            <Table {...getTableProps()} className="shadow-lg p-3 mb-5 bg-white rounded small" striped bordered hover responsive>
            <thead className="bg-primary">
                    {headerGroups.map(headerGroup => (
                    <tr {...headerGroup.getHeaderGroupProps()}>
                        {headerGroup.headers.map(column => (
                        <th
                            {...column.getHeaderProps(column.getSortByToggleProps())}
                        >
                            {column.render('Header')}
                            <span>
                            {column.isSorted ? (column.isSortedDesc ? ' ' : ' ') : ''}
                            </span>
                            <div>{column.allowFilter? column.render('Filter') : null}</div>
                        </th>
                        ))}
                    </tr>
                    ))}
            </thead>
            <tbody {...getTableBodyProps()}>

                    {page.map(row => {
                        prepareRow(row)
                        return (
                            <tr {...row.getRowProps()}>
                            {row.cells.map(cell => {
                                console.log(cell);
                                // if(cell.column.id !="col5"){                                
                                    return (
                                        <td {...cell.getCellProps()}>
                                            {cell.render('Cell')}
                                        </td>
                                        )
                                    // }else{
                                    //     return (
                                    //         <td>
                                    //             {/* <Link to = "/viewMemberDetail/:5" params={{ member_id: cell.value.member_id }}> */}
                                    //             <Button size={"sm"} variant="primary"  onClick={()=>{history.push("/viewMemberDetail",cell.value.member_id)}}>View</Button>
                                    //             {' '}
                                    //             <Button size={"sm"} variant="success" onClick={()=>{history.push("/addMember",cell.value.member_id)}} >Edit</Button>
                                    //             {' '}
                                    //             {/* <Button size={"sm"} variant="danger" onClick={()=>deleteHandle(cell.value.member_id)} >Delete</Button> */}
                                    //         </td>
                                    //     )
                                    // }
                            })}
                            </tr>
                        )
                        })}

            </tbody>
        </Table>
        <div style={{display:"none"}}> <PrintRdApplication ref={emiDetailsRef} dueEmis={rows}/></div>

        <div className="text-right">
          {paginate(pageCount)}
        </div>   
        </Col>
        </Row>
        </Container>
        </>
        );
}

export default RdApplications;